import { Button } from '@popup/shared/components/Button';
import { Error } from '@popup/shared/components/errors/Error';
import { useCancelPremium } from '@popup/shared/hooks/useCancelPremium';
import { clsx } from 'clsx';
import { Link, useLocation } from 'react-router-dom';
import { RoutePath } from '../../../../routes/types/RoutePath';
import { Footer } from '../../../shared/Footer';
import { Loader } from '../../../shared/Loader';
import { PremiumMiniHeader } from '../../../shared/PremiumMiniHeader';
import { type CancelPage } from '../../types/CancelPage';

import styles from './Sorry.module.css';

type SorryProps = {
  page: CancelPage;
};

export function Sorry({ page }: SorryProps): JSX.Element | null {
  const {
    status,
    isSuccess,
    cancelStatus,
    errorMessage,
    isCancelPremiumError,
    isCancelPremiumLoading,
    handleCancelPremium,
  } = useCancelPremium({ RoutePath });

  const location = useLocation();

  if (isCancelPremiumError) {
    return <Error text={errorMessage} footer={<Footer />} />;
  }

  if (isCancelPremiumLoading) {
    return (
      <Loader className={styles.loader}>
        {status === 'Paid'
          ? '   Premium. , ...'
          : '   . , ...'}
      </Loader>
    );
  }

  if (isSuccess) {
    return (
      <div className={styles.success}>
        <h3>  Premium  !</h3>
        {cancelStatus === 'Canceled' ? (
          <p>       1-7 </p>
        ) : null}
        <Link to={RoutePath.Root} tabIndex={-1}>
          <Button
            className={styles.button}
            variant="primary"
            data-attribute="close-button"
          >
               
          </Button>
        </Link>
      </div>
    );
  }

  if (location.pathname === RoutePath.Root) {
    return null;
  }

  return (
    <div className={styles.wrapper} data-attribute="sorry">
      <div
        className={clsx(styles.container, {
          [styles.out]: page !== 'sorry',
        })}
      >
        <PremiumMiniHeader />
        <div className={styles.emoji}></div>
        <div className={styles.info}>
          <h3 className={styles.title}>  ...</h3>
          <br />
          <span className={clsx(styles.goodbye, styles.text)}>
                ,     Premium-
            .
          </span>
          <br />
          <span className={styles.text}>
               ,      ,
                 .
          </span>
        </div>
        <div className={styles.buttons}>
          <Button onClick={handleCancelPremium} variant="primary">
             
          </Button>
          <Link to={RoutePath.Root}>
            <Button variant="secondary" data-attribute="close-button">
               
            </Button>
          </Link>
        </div>
      </div>
    </div>
  );
}
