import { Error } from '../../Error';

type BrowserErrorProps = {
  onClick: () => void;
  header?: React.ReactNode;
  footer?: React.ReactNode;
};
export function BrowserError({
  onClick,
  header,
  footer,
}: BrowserErrorProps): JSX.Element {
  const isNoFAQ: boolean = process.env.IS_NO_FAQ === 'true';
  const title = process.env.EXTENSION_TITLE || '';
  const text = isNoFAQ
    ? ` , ${title}   
  .     ,   
   `
    : ` , ${title}   
  .     ,   
   .      
         `;

  return (
    <Error
      text={text}
      textLink={
        isNoFAQ ? null : (
          <a href={process.env.HELP_URL} target="_blank" rel="noreferrer">
            FAQ
          </a>
        )
      }
      buttonText="Ok"
      onClick={onClick}
      header={header}
      footer={footer}
    />
  );
}
